import {
    createRouter,
    createWebHistory
} from 'vue-router'
import Home from '../views/Home.vue'

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/activities',
        name: 'Activities',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import( /* webpackChunkName: "about" */ '../views/Activities.vue')
    },

    {
        path: '/research',
        name: 'Research',
        component: () =>
            import('../views/Research.vue')
    },
    {
        path: '/academy',
        name: 'Team',
        component: () =>
            import('../views/Team.vue')
    },
    {
        path: '/announcement',
        name: 'Announcement',
        component: () =>
            import('../views/Announcement.vue')
    },
    {
        path: '/contact',
        name: 'Contact',
        component: () =>
            import('../views/Contact.vue')
    },

]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router